
import { computed, defineComponent, onMounted, ref } from "vue";
import { hideModal } from "@/core/helpers/dom";
import { useI18n } from "vue-i18n/index";
import { StepperComponent } from "@/assets/ts/components/_StepperComponent";
import Swal from "sweetalert2/dist/sweetalert2.min.js";
import { useForm } from "vee-validate";
import { Field } from "vee-validate";
import * as Yup from "yup";

interface Step1 {
  invoiceType: string;
}

interface Step2 {
  invoiceNumber: string;
  invoiceDate: string;
  dueDate: string;
  invoiceTo: string;
  invoiceAddress: string;
  invoicePhone: string;
  invoiceEmail: string;
  invoiceNotes: string;
  invoiceTax: number;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  invoiceDiscount: any;
  invoiceTotal: string;
}

interface KTCreateApp extends Step1, Step2 {}

export default defineComponent({
  name: "create-invoice-modal",
  components: {
    Field,
    // ErrorMessage,
  },
  setup() {
    const _stepperObj = ref<StepperComponent | null>(null);
    const createInvoiceRef = ref<HTMLElement | null>(null);
    const createInvoiceModalRef = ref<HTMLElement | null>(null);
    const currentStepIndex = ref(0);
    const paymentMethod = ref(false);
    const toggleNotes = ref(false);
    const paymentUrl = ref("");
    const invoice_date = ref(new Date());
    const invoice_duo_date = ref("");
    const il8n = useI18n();
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const items = ref<any[]>([
      {
        name: "",
        description: "",
        quantity: "1",
        price: "",
      },
    ]);
    const formData = ref<KTCreateApp>({
      invoiceType: "fast",
      invoiceNumber: "",
      invoiceDate: "",
      dueDate: "",
      invoiceTo: "",
      invoiceAddress: "",
      invoicePhone: "",
      invoiceEmail: "",
      invoiceNotes: "",
      invoiceTax: 14,
      invoiceDiscount: "",
      invoiceTotal: "",
    });

    onMounted(() => {
      _stepperObj.value = StepperComponent.createInsance(
        createInvoiceRef.value as HTMLElement
      );
    });

    const addItem = () => {
      items.value.push({
        name: "",
        description: "",
        quantity: "1",
        price: "",
      });
    };

    const removeItem = (index: number) => {
      items.value.splice(index, 1);
    };

    const totalInvoice = computed(() => {
      let total = 0;
      items.value.forEach((item) => {
        total +=
          item.price && item.quantity
            ? parseFloat(item.quantity) * parseFloat(item.price)
            : 0;
      });

      return total;
    });

    const createAppSchema = [
      Yup.object({
        // itemName: Yup.string().required().label("Item Name"),
        // itemQty: Yup.string().required().label("Item Quantity"),
        // itemPrice: Yup.string().required().label("Item Price"),
      }),
    ];

    // extracts the individual step schema
    const currentSchema = computed(() => {
      return createAppSchema[currentStepIndex.value];
    });

    const totalSteps = computed(() => {
      if (!_stepperObj.value) {
        return;
      }

      return _stepperObj.value.totatStepsNumber;
    });

    const { resetForm, handleSubmit } = useForm<Step1 | Step2>({
      validationSchema: currentSchema,
    });

    const previousStep = () => {
      if (!_stepperObj.value) {
        return;
      }

      currentStepIndex.value--;

      _stepperObj.value.goPrev();
    };

    const handleStep = handleSubmit((values) => {
      for (const item in values) {
        // eslint-disable-next-line no-prototype-builtins
        if (values.hasOwnProperty(item)) {
          if (values[item]) {
            formData.value[item] = values[item];
          }
        }
      }

      currentStepIndex.value++;

      if (!_stepperObj.value) {
        return;
      }

      _stepperObj.value.goNext();
    });

    const formSubmit = () => {
      Swal.fire({
        text: "All is cool! Now you submit this form",
        icon: "success",
        buttonsStyling: false,
        confirmButtonText: "Ok, got it!",
        customClass: {
          confirmButton: "btn fw-bold btn-light-primary",
        },
      }).then(() => {
        hideModal(createInvoiceModalRef.value);
        console.log(formData.value);
        resetForm();
        if (!_stepperObj.value) {
          return;
        }
        currentStepIndex.value = 0;
        _stepperObj.value.goFirst();
      });
    };

    resetForm({
      values: {
        ...formData.value,
      },
    });

    return {
      createInvoiceRef,
      totalSteps,
      previousStep,
      handleStep,
      formSubmit,
      paymentMethod,
      paymentUrl,
      toggleNotes,
      addItem,
      removeItem,
      currentStepIndex,
      invoice_date,
      totalInvoice,
      il8n,
      items,
      invoice_duo_date,
      formData,
      createInvoiceModalRef,
    };
  },
});
